import {
    Component,
    EventEmitter,
    Prop,
    h,
    Host,
    Element,
    Event,
} from '@stencil/core';
import { AtuiEvent } from '../../types/events';

export interface ListSelectorItem {
    id: string;
    title: string;
    subtitle?: string;
    icon?: string;
    tooltip?: string;
    prefix?: string;
    badgeText?: string;
    badgeTooltip?: string;
    hasInfoButton?: boolean;
}

/**
 * @category Navigation
 * @description A list selector component for selecting an item from a list.
 *
 * @slot - Placed after the list items
 */
@Component({
    tag: 'atui-list-selector',
    shadow: false,
})
export class AtuiListSelector {
    /**
     * List of items.
     */
    @Prop()
    options: ListSelectorItem[] = [];

    /**
     * The selected item.
     */
    @Prop({ mutable: true })
    selected_item_id: string;

    /**
     * Whether each item has a bottom border.
     */
    @Prop()
    has_border: boolean;

    /**
     * Emits an event when the selected item is changed, the `event.detail` is the ListSelectorItem
     */
    @Event()
    atuiChange: EventEmitter<ListSelectorItem>;

    /**
     * Emits when event when the info button of an item is clicked
     */
    @Event()
    atuiInfoButtonClick: EventEmitter<AtuiEvent>;

    @Element() el: HTMLAtuiListSelectorElement;
    listItemEls: HTMLAtuiListSelectorItemElement[] = [];

    onSelect(item: ListSelectorItem): void {
        this.selected_item_id = item.id;
        this.atuiChange.emit(item);
    }

    onClickInfoButton(event: Event): void {
        this.atuiInfoButtonClick.emit({
            originalEvent: event,
            componentType: 'atui-list-selector',
        });
    }

    focusAndClickRelativeItem(relativePosition: number) {
        const indexOfActiveItem = this.listItemEls.indexOf(
            this.el.ownerDocument
                .activeElement as HTMLAtuiListSelectorItemElement,
        );
        const nextActiveItem =
            this.listItemEls[
                (indexOfActiveItem +
                    relativePosition +
                    this.listItemEls.length) %
                    this.listItemEls.length
            ];
        nextActiveItem.click();
        nextActiveItem.focus();
    }

    handleKeyDown(event: KeyboardEvent) {
        if (event.key === 'ArrowRight' || event.key === 'ArrowDown') {
            event.preventDefault();
            this.focusAndClickRelativeItem(1);
        } else if (event.key === 'ArrowUp' || event.key === 'ArrowLeft') {
            event.preventDefault();
            this.focusAndClickRelativeItem(-1);
        }
    }

    get getListItems(): any {
        return this.options.map((item) => (
            <atui-tooltip
                position="right"
                disabled={!item.tooltip}
            >
                <div slot="tooltip-trigger">
                    <atui-list-selector-item
                        item_id={item.id}
                        item_title={item.title}
                        subtitle={item.subtitle}
                        icon={item.icon}
                        item_prefix={item.prefix}
                        has_border={this.has_border}
                        is_selected={
                            this.selected_item_id &&
                            item.id === this.selected_item_id
                        }
                        onClick={() => this.onSelect(item)}
                        tabindex="0"
                        ref={(el) => this.listItemEls.push(el)}
                    >
                        {item.badgeText && item.badgeTooltip && (
                            <atui-tooltip
                                slot="badge"
                                position="right"
                            >
                                <atui-badge
                                    class="ml-4"
                                    slot="tooltip-trigger"
                                    impact="high"
                                    type="info"
                                    label={item.badgeText}
                                />
                                <span slot="tooltip-content">
                                    {item.badgeTooltip}
                                </span>
                            </atui-tooltip>
                        )}

                        {item.hasInfoButton && (
                            <button
                                slot="info"
                                class="sm"
                                color="primary"
                                onClick={(event) =>
                                    this.onClickInfoButton(event)
                                }
                            >
                                <i class="material-icons">help_outline</i>
                            </button>
                        )}
                    </atui-list-selector-item>
                </div>
                <span slot="tooltip-content">{item.tooltip}</span>
            </atui-tooltip>
        ));
    }

    render() {
        return (
            <Host onKeyDown={(event) => this.handleKeyDown(event)}>
                <slot name="header"></slot>

                {!!this.options.length && (
                    <nav class="flex-fill overflow-visible pb-16">
                        <div
                            class="flex flex-col"
                            role="menu"
                        >
                            {this.getListItems}
                        </div>
                    </nav>
                )}
                <slot />
            </Host>
        );
    }
}
